import React, { Component } from 'react'
import KeyboardEventHandler from 'react-keyboard-event-handler'
import Inko from 'inko'

let inko = new Inko()

class Post extends Component {
  state = {
    inputText: '|',
  }

  componentDidMount() {
    window.addEventListener('keydown', function (e) {
      if (e.keyCode === 32 && e.target === document.body) {
        e.preventDefault()
      }
    })
  }

  applyStroke2State(key) {
    this.setState({ inputText: this.state.inputText.slice(0, -1) })
    switch (key) {
      case 'space':
        this.setState({ inputText: this.state.inputText + ' ' })
        break
      case 'backspace':
        this.setState({ inputText: this.state.inputText.slice(0, -1) })
        break
      default:
        this.setState({ inputText: this.state.inputText + key })
    }
    this.setState({ inputText: this.state.inputText + '|' })
  }

  render() {
    return (
      <div className="App">
        <KeyboardEventHandler
          handleKeys={['alphabetic', 'numeric', 'space', 'backspace']}
          onKeyEvent={(key, e) => this.applyStroke2State(key)}
        />
        <input
          type="text"
          disabled={true}
          autoFocus={true}
          id="output-line"
          value={inko.en2ko(this.state.inputText)}
        />
      </div>
    )
  }
}

export default Post
